.login {
    background-color: #ff4656;
    min-height: 650px;
    padding-top: 30px;
    padding-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    
    &__title {
        padding: 0 0 10px 0;
        margin: 0;
        font-family: 'Oxygen', 'serif';
        font-size: 25px;
    }
}

.wrapperInput {
    margin-bottom: 5px;
    position: relative;
}

.error {
    position: absolute;
    color: #1f2326;
    font-family: 'Oxygen', 'serif';
    font-size: 18px;
    margin: 5px 0;
    top: 5px;
    left: -100%;
}

.inputLogin {
    border: none;
    padding: 10px;
    margin: 0 0 5px 0;
}

.checkbox {
    display: none;
}

.labelCheck {
}

.fakeCheck {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: solid 1px #f1f1f1;
    position: relative;
    background-color: #1F2326;
}

.fakeCheck::before {
    content: "";
    position: absolute;
    display: block;
    width: 10px;
    height: 10px;
    background-color: #f1f1f1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: 0.2s;
}

.checkbox:checked + .fakeCheck::before {
    opacity: 1;
}

.textLabel {
    display: inline-block;
    padding: 5px 0 5px 5px;
}